import { connect } from "react-redux"
import Profile from "./Profile"
import React, { useEffect } from "react";
import { addPostCriator, getUserStatus, updateUserStatus } from "../../redux/profile-reducer";
import { useParams } from "react-router-dom";
import { compose } from "redux";
import withAuthNavigate from "../../hoc/withAuthNavigate";
import { getAutoRizedUserId, getIsAuth, getPosts, getStatus } from "../../redux/profile-selectors";




let ProifleContainer = (props) => {
    let {userId} = useParams();
    if(!userId ) {
        userId = props.autoRizedUserId; 
    };

    

    useEffect(() => {
        props.getUserStatus(userId);
    }, [userId])

    
    
    return (
        
        <Profile {...props} />
    )
}

let mapStateToProps = (state) => {
    
    return {
        status: getStatus(state),
        autoRizedUserId: getAutoRizedUserId(state),
        isAuth: getIsAuth(state),
        posts: getPosts(state),

    };
};
export default compose(
    connect(mapStateToProps, {getUserStatus, updateUserStatus, addPostCriator}),
    withAuthNavigate,
) (ProifleContainer)

 




